"use client"
import { useForm } from "react-hook-form"
import { IUser } from "@/types/types";
import { Dispatch, SetStateAction, useEffect, useState } from "react";
import { Check, Lock, SquarePen } from "lucide-react";

type formType = {
  password: string,
  newPassword: string,
  newPasswordAgain: string,
}

const AccountSettings = ({ user, setEditing, editing }: { user: IUser, editing: string | null, setEditing: Dispatch<SetStateAction<"password" | "personal" | "goals" | null>> }) => {
  const { register, formState: { errors }, handleSubmit } = useForm<formType>({});
  const [isEquals, setIsEquals] = useState<boolean|null>(null);
  const [emailNotEnabled, setEmailNotEnabled] = useState<boolean>(false);
  const [inAppNotEnabled, setInAppNotEnabled] = useState<boolean>(false);
  const handleSave = handleSubmit(async (data) => {
    // Partial - for unnessasary fields
    const payload: Partial<formType> = {};


    if (data.newPassword && data.newPassword === data.newPasswordAgain && data.password) {
      payload.password = data.password;
      payload.newPassword = data.newPassword;
      payload.newPasswordAgain = data.newPasswordAgain;
      if (payload.newPassword !== payload.newPasswordAgain) {
        setIsEquals(false)
        return;
      };
    }

    setEditing(null);

    // if nothing has changed returning;
    if (Object.keys(payload).length === 0) return;


  })

  useEffect(() => {
    if (user) {
      setEmailNotEnabled(user.emailNotifications);
      setInAppNotEnabled(user.inAppNotifications);
    }

  }, [user])

  return (
    <div className=" rounded-[10px]  bg-white py-6 px-8 ">

      <h2 className="section-title mb-6">Account Settings</h2>
      <div className="flex flex-col gap-6">
        <div className="flex items-center justify-between">
          <h3 className="leading-3.5 font-medium ">Email Notifications</h3>
          <button type="button" onClick={() => { setEmailNotEnabled(!emailNotEnabled) }} className={`w-11 h-6  overflow-hidden relative flex items-center cursor-pointer _border rounded-xl p-0.5 ${!emailNotEnabled ? "bg-white" : "bg-green"}`}>
            <div className={`bg-white w-5 h-5 border rounded-full  transform transition-transform duration-300 ${emailNotEnabled ? "translate-x-4.5!" : "translate-x-0 bg-green!"
              }`}></div>
          </button>
        </div>
        <div className="flex items-center justify-between">
          <h3 className="leading-3.5 font-medium ">In-App Notifications</h3>
          <button type="button" onClick={() => { setInAppNotEnabled(!inAppNotEnabled) }} className={`w-11 h-6  overflow-hidden relative flex items-center cursor-pointer _border rounded-xl p-0.5 ${!inAppNotEnabled ? "bg-white" : "bg-green"}`}>
            <div className={`bg-white w-5 h-5 border rounded-full  transform transition-transform duration-300 ${inAppNotEnabled ? "translate-x-4.5!" : "translate-x-0 bg-green!"
              }`}></div>
          </button>
        </div>
        <div className="flex items-center justify-between">
          <h3 className="leading-3.5 font-medium">Change Password</h3>
          <button onClick={() => setEditing(prev => prev == "password" ? null : "password")} type="button" className="button-transparent inline-flex gap-4 bg-black! hover:text-green! text-neutral-200!">
            {editing !== "password" ?
              <>
                <Lock className="" size={16} />
                <span className="text-sm font-medium">Change Password</span>
              </>
              :
              <>
                <Check className="" size={16} />
                <span className="text-sm font-medium">Save</span>
              </>
            }

          </button>
        </div>
      </div>

      {/* FORM FOR PASS CHANGING */}
      {editing === "password" &&
        <form className="py-4 border-t-[1px] border-black mt-6" >

          <h2 className="section-title mb-6">Password Management</h2>
          <div className="grid sm:grid-cols-2 gap-x-4  gap-y-3.5 text-sm">
            <div className="flex flex-col gap-[10px]">
              <label className="leading-3.5 font-medium">Current Password</label>
              <input className="input px-3 py-2"
                type="password"
                {...register("password", {})}

                disabled={editing !== "password"}
              />
            </div>
            <div className="flex flex-col gap-[10px]">
              <label className={`leading-3.5 font-medium `}>New Password</label>
              <input className={`input px-3 py-2 ${isEquals===null ? "" : "border-red-500!"}  `}
                type="password"
                {...register("newPassword", {})}
                disabled={editing !== "password"}
              />
              {isEquals===false && (
                <span data-testid='error' className="text-red-500 font-medium ">
                  Passwords are not similar
                </span>
              )}
            </div>
            <div className="flex flex-col gap-[10px] ">
              
              <label className={`leading-3.5 font-medium `}>Confirm New Password</label>
              <input className={`input px-3 py-2 ${isEquals===null ? "" : "border-red-500!"}`}
                type="password"
                {...register("newPasswordAgain", {})}
                disabled={editing !== "password"}
              />
                {isEquals===false && (
                <span data-testid='error' className="text-red-500 font-medium ">
                  Passwords are not similar
                </span>
              )}
            </div>





          </div>


        </form>
      }

    </div >
  )
}

export default AccountSettings